<script>
import $ from 'jquery';
import Pagination from '../../components/backend/Pagination.vue';

export default {
  data() {
    return {
      coupons: [],
      isLoading: false,
      tempCoupon: {},
      isNew: false,
      isDelete: false,
      pagination: {},
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getCoupons(page = 1) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.isLoading = false;
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
      });
    },
    updateCoupon() {
      let api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon`;
      let httpMethod = 'post';
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
        if (response.data.success) {
          $('#couponModal').modal('hide');
          vm.getCoupons();
        } else {
          $('#couponModal').modal('hide');
          vm.getCoupons();
        }
      });
    },
    openModel(isNew, item) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, item);
        this.isNew = false;
      }
      $('#couponModal').modal('show');
    },
    openDeleteModel(item) {
      $('#delCouponModal').modal('show');
      this.tempCoupon = item;
    },
    deleteCoupon(isDelete) {
      const vm = this;
      if (isDelete) {
        const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon/${vm.tempCoupon.id}`;
        this.$http.delete(api).then((response) => {
          if (response.data.success) {
            vm.tempCoupon = {};
            $('#delCouponModal').modal('hide');
            vm.getCoupons();
          } else {
            vm.tempCoupon = {};
            $('#delCouponModal').modal('hide');
          }
        });
      } else {
        vm.tempCoupon = {};
        $('#delProductModal').modal('hide');
      }
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>

